import { Injectable } from '@angular/core';
import {HttpClient, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {finalize, map} from 'rxjs/operators';
import 'rxjs/add/observable/from';
import 'rxjs/add/operator/concatAll';
import {Objectdiagram} from 'art-viz-shared/dist/out-tsc';
import {pipe} from 'rxjs/util/pipe';

@Injectable()
export class BackendMockService implements HttpInterceptor {
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        if (req.url.includes('api/getModuleAnalysis')) {

            return new Observable<HttpEvent<any>>(observer => {
                    this.mapFunction(null).then(
                        resp => {
                            observer.next(resp);
                            observer.complete();
                        });
                }
            );
        }

        // Default
        return next.handle(req);
    }

    constructor(private httpClient: HttpClient) { }

    private mapFunction(resp: HttpEvent<any>): Promise<HttpResponse<any>> {
        return new Promise<HttpResponse<any>>(resolve => {
            this.getModuleAnalysis().then(
                analysis => {
                    const res = new HttpResponse();
                    resolve(res.clone({body: analysis}));
                }
            );
        });
    }

    private getModuleAnalysis(): Promise<JSON[]> {
        return new Promise((resolve, reject) => {
            this.httpClient.get<string[]>('assets/moduleAnalyses.json').subscribe(
                analyses => {
                    const files = [];
                    const obser = [];
                    for (const file of analyses) {
                        obser.push(this.httpClient.get((file)));
                    }

                    Observable.from(obser).concatAll()
                        .pipe(finalize(
                            () => {
                                resolve(files);
                            }))
                        .subscribe(
                            data => {
                                files.push(JSON.stringify(data));
                            },
                            error => {
                                reject(error);
                            }
                        );
                }
            );
        });
    }

}



