import {Component, OnInit} from '@angular/core';
import {Objectdiagram, Project} from 'art-viz-shared/dist/out-tsc';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {map} from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
    protected selectedProject: Project = new Project('MvW-Analysis', 'assets/MvW-Analysis');
    protected selectedOd: Objectdiagram = new Objectdiagram('', '', false);
    protected odAsJson: JSON;

    ngOnInit(): void {
        this.getMergedJSON().subscribe(
            od => {
                this.httpClient.get<JSON>(od.jsonPath).subscribe(
                    json => {
                        this.odAsJson = json;
                        setTimeout(() => {
                            this.selectedOd = od;
                        }, 0);
                    }
                );
            }
        );
    }

    constructor(private httpClient: HttpClient) {

    }

    private getMergedJSON(): Observable<Objectdiagram> {
        return this.httpClient.get<JSON>('assets/analysedOD.json')
            .pipe(
                map(res => {
                    return new Objectdiagram('02.merged/Merged.od', res['path'], true, res['jsonPath']);
                })
            );
    }

}



